import { Component, ElementRef, Renderer, Input, Output, EventEmitter, DoCheck } from '@angular/core';

import { CharacterObject } from './models/character';
import { Item, ItemStack } from './models/equipment/equipment';
import { CharacterSkill } from './models/character-skill'
import { ICharacterCreationFunctions } from './util/character-creation-functions'
import { AddRemoveMenuComponent } from './add-remove-menu.component'

@Component({
  selector: 'character-sheet',
  templateUrl: './character-sheet.component.html',
  styleUrls: ['./character-sheet.component.css']
})

export class CharacterSheetComponent implements DoCheck {
  private _character: CharacterObject;
  @Input() public creator: ICharacterCreationFunctions;

  maxDamageColumns: number = 10;

  public static readonly DefaultValuesStatic: {
    luck: number;
    reputation: number;

  } = {
    luck: 11,
    reputation: 5
  };


  constructor(private _el: ElementRef, private _re: Renderer) {
    var self = this;
  }

  //private _iCheck = 0;
  ngDoCheck() {
    //console.log("ngDoCheck", this._iCheck)
    //this._iCheck++;
    var self = this;
    Object.keys(this.character.inferedAttributes).forEach((key) => {
      self.character.inferedAttributes[key].updateValue();
    })
    Object.keys(this.character.inferedBaseSkills).forEach((key) => {
      self.character.inferedBaseSkills[key].updateValue();
    })
  }

  private sumWeights(items: Array<Item>) {
    return items.reduce((a, b) => {
      if (b.weight !== null && typeof b.weight !== 'undefined')
        return a + Number(b.weight);
      return a;
    }, 0);
  }

  private sumWeightsStack(items: Array<ItemStack>) {
    return items.reduce((a, b) => {
      if (b.weight !== null && typeof b.weight !== 'undefined')
        return a + Number(b.weight) * b.count;
      return a;
    }, 0);
  }

  formatWeightSum(val, precision = 3) {
    return val !== 0 && typeof val === 'number' ? Number(val.toPrecision(precision)) : null;
  }

  sumWeightBodyArmor(): number {
    return 0.5 * this.sumWeights(this.character.equipment.armor);
  }
  sumWeightWeaponsShields(): number {
    var mapItems = (a) => { return a.item };
    return this.sumWeights(this.character.equipment.meleeWeapons.map(mapItems)) +
      this.sumWeights(this.character.equipment.rangedWeapons.map(mapItems)) +
      this.sumWeights(this.character.equipment.shields.map(mapItems));
  }
  sumWeightCarriedItems(): number {
    return this.sumWeights(this.character.equipment.itemsCarried) + this.sumWeightsStack(this.character.equipment.coins) / 1000;
  }
  sumWeightLuggage(): number {
    return this.sumWeights(this.character.equipment.luggage) + this.character.rations.count * this.character.rations.weight;
  }
  sumWeightTotal(): number {
    return this.sumWeightBodyArmor() + this.sumWeightWeaponsShields() + this.sumWeightCarriedItems() + this.sumWeightLuggage();

  }

  @Input()
  get character() {
    return this._character;
  }

  set character(value) {
    this._character = value;
    if (this.character) {
      this._sortedCharacterSkills = this.sortCharacterSkills(this.character);
      
    }
  }


  private sortCharacterSkills(character: CharacterObject) {
    var sortedCharacterSkills = new Array<CharacterSkill>();

    Object.keys(character.skills).forEach((key) => {
      sortedCharacterSkills.push(character.skills[key]);
    })
    sortedCharacterSkills.sort((a: CharacterSkill, b: CharacterSkill) => { return a.name ? a.name.localeCompare(b.name, document.documentElement.lang) : -1 });
    //TODO setup name value monitorting and update name sort order on name change
    return sortedCharacterSkills;
  }

  public formatDefaultValue(value: number) {
    return `[${value}]`
  }

  get DefaultValues() {
    return CharacterSheetComponent.DefaultValuesStatic;
  }

  private _sortedCharacterSkills: Array<CharacterSkill>;

  get characterSkills() {
    return this._sortedCharacterSkills;
  }
}


