import { Input, Output, EventEmitter } from '@angular/core';
import { Item } from './item';

import { object, serializable } from 'serializr';

export class RangedDamage{
    @serializable public short: string = null;
    @serializable public normal: string = null;
    @serializable public far: string = null;
    @serializable public veryFar: string = null;
}

export class RangedWeapon extends Item {
    @serializable public range: string = null;
    @serializable(object(RangedDamage)) public damage: RangedDamage = new RangedDamage();
}



